<template>
  <div class="hello" style="background-color: rgb(255, 204, 255)">
    <v-col cols="12" sm="12" md="12">
      <v-row>
        <v-card dark style="margin: 10px; width: 100%">
          <v-card-title>
            <div class="text-h4 mb-1">TransReadFinder</div>
          </v-card-title>
          <v-card-text>
            <v-file-input
              multiple
              hide-details
              accept=".json"
              @change="loadExperimentFiles"
            />
          </v-card-text>
        </v-card>
      </v-row>
      <v-row>
        <v-card dark style="margin: 10px; width: 100%">
          <v-card-title> Analysed projects </v-card-title>
          <v-card-text>
            <v-data-table
              dense
              dark
              style="margin: 10px"
              :headers="headers"
              :items="experiment_information"
              :items-per-page="-1"
              :sort-by="['score']"
              :sort-desc="[true]"
              class="elevation-1"
              @click:row="onFragmentRowClicked"
            >
              <template v-slot:[`item.link`]="{ value }">
                <a :href="value" target="blank">
                  {{ value }}
                </a>
              </template>
            </v-data-table>
          </v-card-text>
        </v-card>
      </v-row>
      <v-row>
        <v-card dark style="margin: 10px; width: 100%">
          <v-card-title> Candidates of all projects </v-card-title>
          <v-card-text>
          <v-data-table
            dense
            dark
            style="margin: 10px"
            :headers="headers2"
            :items="candidate_information"
            :items-per-page="-1"
            :sort-by="['genename']"
            :sort-desc="[false]"
            class="elevation-1"
          >
          <template v-slot:item="i">
          <tr>
            <td :class="{'purple lighten-3': i.item.counter > 1}">{{i.item.counter}}</td>
            <td>{{i.item.projectID}}</td>
            <td>{{i.item.proteinid}}</td>
            <td>{{i.item.genename}}</td>
            <td>{{i.item.score}}</td>
            <td>{{i.item.sequence_coverage}}</td>
            <td>{{i.item.offtargets}}</td>
            <td>{{i.item.peptide}}</td>
            <td>{{i.item.stopcodon}}</td>
            <td>{{i.item.downstream}}</td>
          </tr>
        </template>

          </v-data-table>
          </v-card-text>
        </v-card>
      </v-row>
    </v-col>
  </div>
</template>

<script>
export default {
  data() {
    return {
      headers: [],
      headers2:[],
      experiment_information: [],
      candidate_information: [],
    };
  },
  components: {},
  mounted() {
    this.headers = [
      {
        text: "Project",
        value: "projectID",
      },
      { text: "Number of Experiments", value: "no_of_experiments" },
      {
        text: "Title",
        align: "start",
        sortable: true,
        value: "title",
      },
      {
        text: "Organism",
        sortable: true,
        value: "organism",
      },
      {
        text: "Disease",
        sortable: true,
        value: "disease",
      },
      {
        text: "Tissue",
        sortable: true,
        value: "tissue",
      },
      {
        text: "Instrument",
        sortable: true,
        value: "instrument",
      },
      {
        text: "Affiliation",
        value: "university",
      },
      {
        text: "Link",
        value: "link",
      },
      {
        text: "Number of Candidates",
        value: "no_of_candidates",
      },
      {
        text: "LDHB",
        value: "ldhb_found",
      },
      {
        text: "MDH1",
        value: "mdh1_found",
      },
      {
        text: "TGA [%]",
        value: "percentage_of_tga",
      },
    ];
    this.headers2 = [
      {
        text: "No. of projects",
        value: "counter",
      },
      {
        text: "Experiment",
        value: "projectID",
      },
      {
        text: "Protein ID",
        value: "proteinid",
      },
      {
        text: "Gene name",
        value: "genename",
      },
      {
        text: "Score",
        align: "start",
        sortable: true,
        value: "score",
      },
      {
        text: "Sequence coverage [%]",
        sortable: true,
        value: "sequence_coverage",
      },
      {
        text: "Offtargets",
        sortable: true,
        value: "offtargets",
      },
      {
        text: "Fragment",
        value: "peptide",
      },
      {
        text: "Stopcodon",
        value: "stopcodon",
      },
      {
        text: "Downstream",
        value: "downstream",
      },
    ];
     this.updateExperimentInfo();
  },
  methods: {
    updateExperimentInfo() {
      let project_information = this.$store.state.data;
      this.experiment_information = [];
      this.candidate_information = [];
      let found_genes = [];
      let counter = 1;
      for (const projectID in project_information) {
        const project = project_information[projectID];
        const fragment_information = project.fragment_information;
        const no_of_candidates = Object.keys(fragment_information).length;
        this.experiment_information.push({
          projectID: projectID,
          title: project.title,
          organism: project.organism,
          disease: project.disease,
          tissue: project.tissue
          .replace("[", "")
          .replace("]", "")
          .replaceAll("'", ""),
          instrument: project.instrument,
          university: project.university,
          link: project.link,
          no_of_candidates: no_of_candidates,
          ldhb_found: project.ldhb_found,
          mdh1_found:  project.mdh1_found,
          percentage_of_tga: project.percentage_of_tga,
          no_of_experiments: project.no_of_experiments,
        });
        
        for(const fragment of fragment_information){
          const genename = fragment.genename;
          if(genename in found_genes){
            counter += 1;
          }
          else{
            found_genes.push(genename);
          }
          console.log('found',found_genes, counter);
          this.candidate_information.push({
            counter: counter,
            projectID: projectID,
            proteinid: fragment.proteinid,
            genename: genename,
            score: fragment.score,
            sequence_coverage: fragment.sequence_coverage,
            offtargets: fragment.offtargets,
            peptide: fragment.peptide,
            stopcodon: fragment.stopcodon,
            downstream: fragment.downstream,
          });
          console.log('candidates', fragment, fragment.id);
        }
      }
    },
    loadExperimentFiles(e) {
      const files = [...e];
      for (let i = 0; i < files.length; i++) {
        const reader = new FileReader();
        reader.onload = (result) => {
          const data = JSON.parse(result.target.result);
          this.$store
            .dispatch("processProject", data)
            .finally(() => this.updateExperimentInfo());
        };
        reader.readAsText(files[i]);
      }
    },
    onFragmentRowClicked(item) {
      this.$router.push("/experiment/" + item.projectID);
    },
  },
};
</script>
